<template>
  <div class='wrapper'>
    <div class='display'>
      <div v-if="invalidConfig">
        <h1>{{ message }}</h1>
      </div>
      <div v-else>
        <Header :headerStyle="config.header"/>
        <Table :tableStyle="config.table" />
      </div>
    </div>

    <div class='config'>
      <textarea @input="onConfigChange($event.target.value)" v-once>{{ config }}</textarea>
    </div>
  </div>
</template>

<script>
import Header from './Header'
import Table from './Table'
import readConfig from '../services/configFileInterpreter';

import isEmpty from 'lodash/isEmpty';

export default {
  name: 'Container',
  components: {
    Header,
    Table
  },
  data() {
    return {
      config: {
        header: {
          color: 'black',
          'font-size': '20px'
        },
        table: {
          table: {
            width: '100%'
          },
          header: {
            'background-color': 'teal',
            color: 'white'
          }
        }
      },
      invalidConfig: false,
      message: ''
    }
  },
  methods: {
    onConfigChange: function(value) {
      try {
        this.config = readConfig(value);
        this.invalidConfig = false;
      }
      catch(e) {
        this.invalidConfig = true;
        this.message = e.message;
      }
    }
  }
}
</script>

