import isEmpty from 'lodash/isEmpty';
import keys from 'lodash/keys';
import includes from 'lodash/includes';

function throwError(msg) {
    throw {
        message: msg
    };
}

function validateConfig(config) {
    if(isEmpty(config)) {
        throwError('No Configuration Detected');
    }

    const configKeys = keys(config);

    if(configKeys.length > 2) {
        throwError('Invalid Configuration Format');
    }

    if(!includes(configKeys, 'header')) {
        throwError('No header configuration');
    }

    if(!includes(configKeys, 'table')) {
        throwError('No table configuration');
    }
}

export default function(config) {
    const json = JSON.parse(config);

    validateConfig(json);

    return json;
}